<template>
	<div id="Pos">
		<div>
			<el-row>
				<el-col :span="8" class="pos-order" id="order-list">
					<el-tabs type="border-card">
						<el-tab-pane label="点餐">
							<el-table :data="tableData" border style="width: 100%">
								<el-table-column prop="goodsName" label="商品名称"></el-table-column>
								<el-table-column prop="count" label="数量" width='50'></el-table-column>
								<el-table-column prop="price" label="金额" width='70'></el-table-column>
								<el-table-column prop="action" label="操作" fixed='right' width='100'>
									<template slot-scope="scope">
										<el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
										<el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
									</template>
								</el-table-column>
							</el-table>
							<div class="total">
								<p>
									数量：<span class="count">{{ totalCount }} </span> 
									金额：<span>￥{{ totalMoney }}元</span>
								</p>
							</div>
							<div class="btn-list">
								<el-button type="warning">挂单</el-button>
								<el-button type="danger" @click="delAllGoods">删除</el-button>
								<el-button type="success" @click="checkOut">结账</el-button>
							</div>
						</el-tab-pane>
						<el-tab-pane label="挂单">挂单</el-tab-pane>
						<el-tab-pane label="外卖">外卖</el-tab-pane>
					</el-tabs>
				</el-col>
				<el-col :span="16" class="pos-goods" id="good-list">
					<div class="often-goods clearfix">
						<div class="title">常用商品</div>
						<div class="often-goods-list">
							<ul>
								<li v-for="goods in goodsData" @click="addOrderList(goods)">
									<span>{{ goods.goodsName }}</span>
									<span class="o-price">￥{{ goods.price }}元</span>
								</li>
							</ul>
						</div>
					</div>
					<div class="goods-type">
						<el-tabs>
							<el-tab-pane label="汉堡">
								<div>
									<ul class="cookList">
										<li v-for="goods in type0Goods" @click="addOrderList(goods)">
											<span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
											<span class="foodName">{{ goods.goodsName }}</span>
											<span class="foodPrice">￥{{ goods.price }}元</span>
										</li>
									</ul>
								</div>
							</el-tab-pane>
							<el-tab-pane label="小食">
								<div>
									<ul class="cookList">
										<li v-for="goods in type1Goods" @click="addOrderList(goods)">
											<span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
											<span class="foodName">{{ goods.goodsName }}</span>
											<span class="foodPrice">￥{{ goods.price }}元</span>
										</li>
									</ul>
								</div>
							</el-tab-pane>
							<el-tab-pane label="饮料">
									<div>
									<ul class="cookList">
										<li v-for="goods in type2Goods" @click="addOrderList(goods)">
											<span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
											<span class="foodName">{{ goods.goodsName }}</span>
											<span class="foodPrice">￥{{ goods.price }}元</span>
										</li>
									</ul>
								</div>
							</el-tab-pane>
							<el-tab-pane label="套餐">
									<div>
									<ul class="cookList">
										<li v-for="goods in type3Goods" @click="addOrderList(goods)">
											<span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
											<span class="foodName">{{ goods.goodsName }}</span>
											<span class="foodPrice">￥{{ goods.price }}元</span>
										</li>
									</ul>
								</div>
							</el-tab-pane>
						</el-tabs>
					</div>
				</el-col>
			</el-row>
		</div>
	</div>
</template>
<script>
	import axios from 'axios';
	export default {
		name:'Pos',
		data(){
			return {
				totalCount:0, //购买数量汇总
				totalMoney:0,  //金额汇总
				tableData:[
					/*{
						goodName:'可口可乐',
						price:6,
						count:2
					},
					{
						goodName:'香辣鸡腿堡',
						price:15,
						count:1
					},
					{
						goodName:'爱心薯条',
						price:8,
						count:1
					},
					{
						goodName:'甜筒',
						price:8,
						count:1
					}*/
				],
				goodsData:[
					/*{
		              goodsId:1,
		              goodsName:'香辣鸡腿堡',
		              price:18
		         	}, {
		              goodsId:2,
		              goodsName:'田园鸡腿堡',
		              price:15
		          }, {
		              goodsId:3,
		              goodsName:'和风汉堡',
		              price:15
		          }, {
		              goodsId:4,
		              goodsName:'快乐全家桶',
		              price:80
		          }, {
		              goodsId:5,
		              goodsName:'脆皮炸鸡腿',
		              price:10
		          }, {
		              goodsId:6,
		              goodsName:'魔法鸡块',
		              price:20
		          }, {
		              goodsId:7,
		              goodsName:'可乐大杯',
		              price:10
		          }, {
		              goodsId:8,
		              goodsName:'雪顶咖啡',
		              price:18
		          }, {
		              goodsId:9,
		              goodsName:'大块鸡米花',
		              price:15
		          }, {
		              goodsId:20,
		              goodsName:'香脆鸡柳',
		              price:17
		          }*/
				],
				type0Goods:[
					/*{
		              goodsId:1,
		              goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg",
		              goodsName:'香辣鸡腿堡',
		              price:18
			          }, {
			              goodsId:2,
			              goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
			              goodsName:'田园鸡腿堡',
			              price:15
			          }, {
			              goodsId:3,
			              goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos004.jpg",
			              goodsName:'和风汉堡',
			              price:15
			          }, {
			              goodsId:4,
			               goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
			              goodsName:'快乐全家桶',
			              price:80
			          }, {
			              goodsId:5,
			               goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
			              goodsName:'脆皮炸鸡腿',
			              price:10
			          }, {
			              goodsId:6,
			               goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos004.jpg",
			              goodsName:'魔法鸡块',
			              price:20
			          }, {
			              goodsId:7,
			               goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg",
			              goodsName:'可乐大杯',
			              price:10
			          }, {
			              goodsId:8,
			               goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
			              goodsName:'雪顶咖啡',
			              price:18
			          }, {
			              goodsId:9,
			               goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
			              goodsName:'大块鸡米花',
			              price:15
			          }, {
			              goodsId:20,
			               goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg",
			              goodsName:'香脆鸡柳',
			              price:17
			          }*/
				],
				type1Goods:[],
				type2Goods:[],
				type3Goods:[],

			}
		},
		mounted:function(){
			var orderHeight = document.body.clientHeight;
			// console.log(orderHeight);
			document.getElementById('order-list').style.height=orderHeight+'px';
		},
		methods:{
			//添加订单列表的方法
			addOrderList(goods){
				//商品是否已经存在于商品列表中
				let isHave =false;
				for(let i=0;i<this.tableData.length;i++){
					if(this.tableData[i].goodsId==goods.goodsId){
						isHave =true;
					}
				}

				//根据判断编写业务逻辑
				//根据isHave的值判断订单列表中是否已经有此商品
				if(isHave){
					//存在就进行数量添加
					let arr = this.tableData.filter(o=>o.goodsId==goods.goodsId);
					arr[0].count++;
				}else{
					//不存在就推入数组
					let newGoods = {
						goodsId:goods.goodsId,
						goodsName:goods.goodsName,
						price:goods.price,
						count:1
					};
					this.tableData.push(newGoods);
				};
				this.getAllCountAndMoney();
			
			},
			//删除单个商品 delSingleGoods(goods)
			delSingleGoods(goods){
				console.log(goods);
				this.tableData = this.tableData.filter(o=>o.goodsId!=goods.goodsId);
				this.getAllCountAndMoney();
			},
			//删除所有的商品
			delAllGoods(){
				this.tableData = [];
				this.totalMoney = 0;
				this.totalCount = 0;
			},

			//模拟结账
			checkOut(){
				if(this.totalCount!=0){
					this.tableData = [];
					this.totalMoney = 0;
					this.totalCount = 0;
					this.$message({
						message:'结账成功！',
						type:'success'
					})
				}else{
					this.$message.error("亲：您还没有选择任何商品哦，赶紧去选择商品吧！");
				}
				
			},

			//汇总数量和金额
			getAllCountAndMoney(){
				this.totalCount = 0; //汇总数量清0
				this.totalMoney = 0;
				if(this.tableData){
					this.tableData.forEach((goods)=>{
					 	this.totalCount +=goods.count;
					 	this.totalMoney = this.totalMoney + (goods.price*goods.count);
					});
				}
			},
		},
		created(){
			axios.get('http://jspang.com/DemoApi/oftenGoods.php')
			.then(response=>{
				// console.log(response.data);
				this.goodsData = response.data;
			})
			.catch(error=>{
				console.log(error);
				 alert('网络错误，不能访问');
			});

			//读取分类商品列表
			axios.get('http://jspang.com/DemoApi/typeGoods.php')
			.then(response=>{
			 console.log(response.data[1]);
			 //this.oftenGoods=response.data;
			 this.type0Goods=response.data[0];
			 this.type1Goods=response.data[1];
			 this.type2Goods=response.data[2];
			 this.type3Goods=response.data[3];

			})
			.catch(error=>{
			  console.log(error);
			  alert('网络错误，不能访问');
			})
		}
	}
</script>
<style scoped>
	.clearfix:after{
		content:'';
		width:0;
		height:0;
		display:block;
		clear:both;
	}
	.pos-order{
		background-color:#fff;
		border-right:1px solid #ddd;
		height:100%;
	}
	.pos-goods{
		
	}
	.btn-list{
		margin-top:20px;
	}
	.title{
		text-align:left;
		background-color:#a0c0a0;
		padding:10px;
	}
	.often-goods-list li{
		float:left;
		width:20%;
		height:40px;
		line-height:40px;
		margin:10px;
		border:1px solid #E5E9F2;
		background-color:#fff;
		cursor:pointer;
	}
	.o-price{
		color:#58B7FF;
	}
	.total{
		font-size:14px;
	}
	.total span.count{
		margin-right:12px;
	}

	.goods-type{
		padding:15px;
	}
	.cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
       cursor:pointer;
   }
   .cookList li span{
       
        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 14px;
       padding-left: 10px;
       color:brown;
 
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
</style>


